import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import Bounce from 'react-reveal/Bounce';


const Div = styled.div`
  max-width: 100vw;
   
  .articles {
    min-height: 70vh;
    position: relative;
    justify-content: center;
    align-items: center;
    margin: 12%;
  }

  .articles .heading {
    font-size: 3.7rem;
    color: #2f3640;
    line-height: 60px;
    margin-bottom: 30px;
    text-align: center;
  }

  .container{
    
    display: inline-flex;
  }

  .container .card {
    display: grid;
    grid-template-columns: 300px;
    grid-template-rows: 210px 100px 50px;
    grid-template-areas: "image" "title" "link";
    box-shadow: 0 10px 10px rgba(46, 204, 113, 0.2);
    
  
    border-radius: 18px;
    background: #fff;
    text-align: center;
    
  }

  .card .card-image img {
    height: 210px;
    width: 300px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    background-size: cover;
  }

  .card-image {
    grid-area: image;

  }

  .card-title {
    grid-area: title;
    margin: 25px;
  }

  .card-title h1 {
    color: #2f3640;
    font-size: 17px;
    font-weight:700;
  }

  .card-link {
    grid-area: link;
    background-color: #2ecc71;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    color: #fff;
  }

  .card-link h3 {
    margin-top: 12px;
    font-weight: 400;
  }

  .card-link h3:hover {
  transform: scale(1.2);
  }

`




function Article() {

  const [blogs, setBlogs] = useState([])


  useEffect(() => {
    fetch('https://api.rss2json.com/v1/api.json?rss_url=https://medium.com/feed/@divineogbe')
      .then(resp => resp.json())
      .then(data => {
        setBlogs(data.items)
      })
  }, [])

  
  return (
    <Div>
      <Bounce bottom>
      <div className="articles">
        <h1 className="heading">Articles</h1>
        {blogs.map(blog => {
          return (
            <div className="container"> 
              <div className="card">
                <div className="card-image">
                  <img src={blog.thumbnail} alt={blog.title} />
                </div>
                <div className="card-title">
                  <h1>{blog.title}</h1>
                </div>
                <div className="card-link">
                  <h3><a style={{color:'inherit'}} target="_blank" rel="noopener noreferrer" href={blog.link}>View Article</a></h3>
                </div> 
              </div>
            </div>
            
          )
        })}
      </div>
      </Bounce>
    </Div>
  );
}

export default Article;