import React from 'react';
import img from '../../images/Cover.png'


import styled from 'styled-components';

const Div = styled.div`
  .body {
  
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
  color: #777;
  padding: 20px;
  max-width: 100vw;
  overflow: hidden;
}



.head{
 height: 100vh;
 background-image: url(${img}) ;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: top;
  position: relative;

  /* clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%); */
}




.text-box{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  align-items: center;
  justify-content: center;
}

.heading-primary {
  font-size: 50px;
  color: #000;
  background: #fff;
  opacity: 0.9;
  backface-visibility: hidden;
  margin: 20px;
}

.heading-primary-main {
  display: block;
  font-size: 60px;
  font-weight: 400;
  

  animation-name: moveInLeft;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  /* animation-delay: 1s; */
}

.text-box p {
  display: block;
  color: #000;
  background: #fff;
  opacity: 0.9;
  font-size: 20px;
  font-weight: 700;
  /* letter-spacing: 10px; */
  animation: moveInRight 1s ease-out;
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }

  80% {
    transform: translateX(10px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveInRight {
  0% {
    opacity: 0;
    transform: translateX(100px);
  }

  80% {
    transform: translateX(-10px);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}

@keyframes moveInBottom {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }

 
  100% {
    opacity: 1;
    transform: translate(0);
  }
}

.btn:link,
.btn:visited {
  text-transform: uppercase;
  text-decoration: none;
  padding: 15px 40px;
  display: inline-block;
  border-radius: 100px;
  transition: all .2s;
  position: relative;
}

.btn:hover {
  color: #fff;
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn:active {
  transform: translateY(-1px) ;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.btn-green {
  background-color: #2ecc71;
  color: #fff;
}

.btn:after {
  content: "";
  display: inline-block;
  height: 100%;
  width: 100%;
  border-radius: 100px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: all .4s;
}

.btn-green::after {
  background-color: #2ecc71;
}

.btn:hover::after {
  transform: scaleX(1.4) scaleY(1.6);
  opacity: 0;
}

.btn-animated {
  animation: moveInBottom .5s ease-out .75s;
  animation-fill-mode: backwards;
}

.row {
  max-width: 1140px;
  margin: 0 auto;
}

.icon-big {
  font-size: 200%;
  display: block;
  color: #2ecc71;
  margin-bottom: 10px;
}


/* ============================
      RESPONSIVENESS
============================ */

@media (max-width:1050px) {
  .head{
    background-size: cover;
    background-position: top left;
  }
}


@media (max-width:700px) {
  .head{
    max-width: 100vw;
    background-size: cover;
    background-position: top left;

  }

  .heading-primary-main, 
  .heading-primary-sub {
    margin-left: 15px;
  }

  .heading-primary-main {
    font-size: 40px;
    font-weight: 400;
    
  }

  .heading-primary-sub {
    font-size: 15px;
    font-weight: 400;
    
  }
}
`



const Header = () => {
  return (
    <Div>
      <div className="head">
         <div className="body text-box" >
          <h1 className="heading-primary">Hello, I am Divine
          </h1>
          <p>Frontend developer and tech content creator living in Lagos Nigeria. </p>
          <a href="http://localhost:3000/contact" className="btn btn-green btn-animated">Say Hi</a>
        </div>
      </div>
    </Div>
  )
};

export default Header;