import React from 'react';
import {DropbarContainer, DropbarWrapper, DropbarMenu, DropbarLink} from './NavbarElements';




const Dropbar = ({isOpen, toggle}) => {
  

  return (
    
    
    <DropbarContainer isOpen={isOpen} onClick={toggle}  >
      <DropbarWrapper>
        <DropbarMenu>
          <DropbarLink to='about' onClick={toggle}>
            About
          </DropbarLink>

          <DropbarLink to='articles' onClick={toggle}>
            Articles
          </DropbarLink>

          <DropbarLink to='say-hi' onClick={toggle}>
            Say Hi!
          </DropbarLink>

       </DropbarMenu>

      </DropbarWrapper>
    </DropbarContainer>
  
  );
};

export default Dropbar;

