import React from 'react';
import Wave from '../../images/emoji.png'
import styled from 'styled-components';
import Bounce from 'react-reveal/Bounce';

const Div = styled.div`
    max-width: 100vw;
    width: 90%;
    align-items: center;
    justify-content: center;
    

  .say-hi {
    min-height: 70vh;
    
  }

  .say-hi .heading {
    display: flex;
    margin-left: 10%;
    margin-bottom: 40px;
  }

  .say-hi .heading  h1 {
    font-size: 50px;
    color: #2f3640;
  
  }

  .say-hi .heading  img{
    height: 55px;
  }

  .contact {
    
    background-color: #fff;
    display: flex;
    align-items: center;
  }

  .contact .contact-info {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  .contact .contact-info .card {
    position: relative;
    flex: 1;
    max-width: 300px;
    height: 140px;
    background-color: #fff;
    margin: 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
    
    border-top: 2px solid #000;
    box-shadow: 0px 10px 10px rgba(46, 204, 113, 0.2);
  }

  .card .icon {
    font-size: 32px;
    color:#2ecc71; 
    transition: 0.3s linear;
  }

  .contact-info .card:hover .icon{
    transform: scale(4);
    opacity: 0;
  }

  .card-content h3,
  .card-content span {
    position: absolute;
    opacity: 0;
    left: 50%;
    transform: translateX(-50%);
    font-size: 16px;
  }

  .card-content h3 {
    top: 20px;
    text-transform: uppercase;
    color: #2ecc71;
  }

  .card-content span {
    bottom: 20px;
    color: #000;
    font-weight: 300;
  }

  .card:hover h3 {
    opacity: 1;
    bottom: 46px;
    transition: .3s linear .3s;
  }

  .card:hover span {
    opacity: 1;
    bottom: 46px;
    transition: .3s linear .3s;
  }

  @media screen and (max-width:900px) {
    .contact .contact-info .card {
      flex: 100%;
      max-width: 500px;
    }
  }
`




const Contact  = () => {
  
  return(
    <Div>
      <Bounce bottom>
      <div className="say-hi">
        <div className="heading">
          <h1>Say Hi</h1>
          <img alt="me" src={Wave}  />
        </div>
        
        <div className="contact">
        
          <div className="contact-info">
            <div className="card">
              <i className="envelope icon"></i>
              <div className ="card-content">
                <h3>Email</h3>
                <span>Dejodev@outlook.com</span>
              </div>
            </div>

            <div className="card">
              <i className="phone icon"></i>
              <div className ="card-content">
                <h3>Phone</h3>
                <span>+234 810 1841 835</span>
              </div>
            </div>

            <div className="card">
              <i className="location arrow icon"></i>
              <div className ="card-content">
                <h3>Location</h3>
                <span>Lagos</span>
              </div>
            </div>
          </div>
        </div>

      </div>
      </Bounce>
    </Div>
    
    
  )
}



export default Contact;
