import React from 'react';
import styled from 'styled-components';
import Img from '../../images/ogbe.png'
import Zoom from 'react-reveal/Zoom';


const Div = styled.div`
  .about{
    display: grid;
    grid-template-areas: 'left-section right-section';
    gap: 40px;
  }

  .left-section{
    background: url(${Img});
    background-color: #fff;
    min-height: 100vh;
    width: 100vh;
    grid-area: left-section;
    background-size: cover;
    background-position: center;

    clip-path: circle(50% at 50% 50%);
  }

  .about .left-section img {
    display: none;
  }

  .basic-information h1{
    font-size: 70px;
    margin: 0;
  }

  .basic-information h1 span {
    font-size: 40px;
    color: #000;
  }

  .basic-information p{
    font-size: 30px;
    margin-bottom: 0;
  }

  .basic-information a {
    color: black;
  }

  .basic-information .icon{
    font-size: 20px;
    margin-bottom: 50px;
    margin-left: 15px;
  }

  .basic-information .icon:hover{
    color: #2ecc71;
  }


  .right-section{
    padding:20px 10px 20px 10px;
    grid-area: right-section;
  }

  .right-section .bio h2{
    font-size: 50px;
    margin-bottom: 1px;
    font-weight: 800;
  }

  .right-section .bio p{
    text-align: justify;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 30px;
  }


  .right-section .bio h2 span{
    color: black;
  }

 

  .skills .app-icons {
    display: flex;
    background-color: #f5f5f5;
    width: 100%;
    height: 100%;
    padding: 10px 10px 10px 10px;
    border-radius: 20px;
    font-size: 50px;
  }

  .skills .app-icons li{
    
    margin-left: 10px;
    margin-top: 20px;
  }


  .about h1,
  .about h2 {
    color: #2ecc71;
    font-family: 'Poppins';
    font-weight: 500;
  }

  .dash {
    min-height: 5px;
    width: 40px;
    background-color: #2ecc71;
    margin-bottom: 20px;
    margin-top: 5px;
  }



  /* ===============================
  =====Responsiveness============
  =============================== */


  @media (max-width:900px) {
    .about{
      grid-template-areas: 
      'left-section'
      'right-section';
      gap: 20px;
      width: 90%;
      margin: auto;
      
    }
    
    .left-section{
      position: relative;
      height: 40vh;
      min-height: 40vh;
      width: 40vh;
      margin-left: 1%;
      margin-top: 10%;

      clip-path: circle(50% at 50% 50%);
    }

    .basic-information h1{
      font-size: 40px;
      margin: 0;
    }
    .basic-information h1 span {
      font-size: 25px;
      color: #000;
    }

    .basic-information p{
      font-size: 20px;
      margin-bottom: 0;
    }

  }

  @media (max-width:1024px) {
    .about{
      grid-template-areas: 
      'left-section'
      'right-section';
      gap: 20px;
      width: 90%;
      margin: auto;
    }
    
    .left-section{
      position: relative;
      height: 40vh;
      min-height: 40vh;
      width: 40vh;
      margin-left: 1%;
      margin-top: 10%;

      clip-path: circle(50% at 50% 50%);
    }
  }
`







const About = () => {
  return(
    <Div>
    <Zoom bottom>
    <div className="about">
        <div className="left-section"></div>
          <div className="right-section">
            <div className="bio">
                <h2>About <span>Me</span></h2>
                <div className="dash"></div> 
                <p>Hello! I am Divine and I am a software engineer working on web and phone applications. I have been developing professionally for a good time. I started in tech as  self-taught freelance developer and part time positons during grad school. </p>
            </div>
              
            <div className="skills">
              <ul className="app-icons">
                  <li><a style={{color:'inherit'}} target="_blank" rel="noopener noreferrer" href='https://twitter.com/Fumes__'><i className="twitter icon"/></a></li> 
                  <li><a style={{color:'inherit'}} target="_blank" rel="noopener noreferrer" href='https://www.linkedin.com/in/divine-ogbe-7a52b9a9/'><i className="linkedin alternate icon"/></a></li> 
                  <li><a style={{color:'inherit'}} target="_blank" rel="noopener noreferrer" href='https://www.instagram.com/__fumes/'><i className="instagram icon"/></a></li> 
                  <li><a style={{color:'inherit'}} target="_blank" rel="noopener noreferrer" href='https://github.com/TheDejo'><i className="github icon"/></a></li> 
              </ul> 
            </div>
            
          </div>
    </div>
    </Zoom>
    </Div>
  )
};

export default About;