import styled from 'styled-components'
import {Link as LinkR} from 'react-router-dom';
import {Link as LinkS} from 'react-scroll';

export const Nav = styled.nav`
  background: #fff;
  max-width: 100vw;
  height: 60px;
  /* margin-top: -80px; */
  display: flex;
  justify-content: center;
  align-items:center;
  font-size: 1rem;
  position: sticky;
  top: 0;
  z-index: 10;

  @media screen and (max-width: 960px) {
    transition: 0.8s all ease;
  }
`;

export const NavbarContainer = styled.div`
  display: flex;
  justify-content: space-between;
  height: 65px;
  z-index: 1;
  width: 100%;
  padding: 0 24px;
  max-width: 1100px;
`;
 
export const NavLogo = styled(LinkR)`
  justify-self: start;
  cursor: pointer;
  display: flex;
  align-items:center;
  
  margin-left: 1px;
  
   img {
    height: 58px;
   }
`;

export const NavMenu = styled.ul`
  display: flex;
  align-items: center;
  list-style: none;
  text-align: center;
  margin-right: -22px;


  @media screen and (max-width: 768px) {
    display: none;
  }
`;

export const NavItem = styled.li`
  height: 60px;
`;

export const NavLinks = styled(LinkS)`
  color: #000;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 1rem;
  font-size: 20px;
  font-weight: 300;
  height: 100%;
  cursor: pointer;


  &.active {
    background-color: #2ecc71;
    color: #fff;
  }

  &:hover {
    color: #2ecc71;
    transition: all 0.1s ease-out;
    
  }
`;

export const ColorMode = styled.div`

  color: #000;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 1rem;
  font-size: 18px;
  height: 100%;
  cursor: pointer;

  @media screen and (max-width: 768px) {
    display: none;
  }
`;

// =======================================
// ===============SIDEBAR=================
// =======================================


export const DropbarContainer = styled.aside`
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: #0d0d0d;
  opacity: 0.9;
  display: none;
  align-items:center;
  margin-top: 60px;
  top: 0;
  left: 0;
  transition: 0.3s ease-in-out;
  transform: ${({isOpen}) => isOpen ? 'translateX(0)' : 'translateX(100%)' };
  /* opacity: ${({isOpen}) => (isOpen ? '100%' : '0')};
  top: ${({isOpen}) => (isOpen ? '0' : '.100')}; */

  @media screen and (max-width: 768px) {
    display: grid;
  }

`;

export const DropbarWrapper = styled.div`
  color: #fff;
`;

 export const DropbarMenu = styled.ul`
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(4, 80px);
  text-align: center;


  @media screen and (max-width: 480px) {
    grid-template-rows: repeat (6, 60px);
  }
 `

export const DropbarLink = styled(LinkS)`
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  text-decoration: none;
  list-style: none;
  transition: 0.2s ease-ease-in-out;
  text-decoration: none;
  cursor: pointer;

  &:hover {
    color: #2ecc71;
    transition: 0.25 ease-in-out;
  }
`;

